<script>
  import {mapState, mapGetters} from 'vuex';
  import orginatordialog from '../dailog/originator_dailog.vue'
  import flowchartdialog from '../dailog/flowchart_dialog.vue'
  import processSelection from '../dailog/processSelection.vue'
  import util from '../../js/util'
  import moment from 'moment'
  import page from "../../plugins/page/index";
  import CascaderOrganize from "../../plugins/cascaderOrganize/index";
  import loading from '../../plugins/loading/loading';
  import Choosen from '../biz/compositeChoosen.vue'
  export default {
    name: 'ccToMeProcess',
    components: {
      'originator_dailog': orginatordialog,
      'processSelection': processSelection,
      'flowchart_dialog': flowchartdialog,
      'page': page,
      Choosen
    },
    mounted() {
      util.burialPoint('抄送我的-进入抄送我的页面');
      this.getList();
      var that = this;
      window.onmessage = function (e) {
        var data = e.data;
        // 点击确认按钮的key
        if (data.key === 'refreshNewSystem') {
          that.getList();
        }
      };
    },
    data() {
      return {
        originatorLoading: false,
        // 日期禁用  handtDate
        pickerOptionsBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.model.startDate[1];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") > beginDateVal;
            }
          }
        },
        pickerOptionsAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.model.startDate[0];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") < beginDateVal;
            }
          }
        },

        // 日期禁用  handtDate
        submitDatepickerOptionsBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.model.submitDate[1];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") > beginDateVal;
            }
          }
        },
        submitDatepickerOptionsAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.model.submitDate[0];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") < beginDateVal;
            }
          }
        },
        // 日期禁用
        readDatedpickerOptionsBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.model.Approve[1];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") > beginDateVal;
            }
          }
        },
        readDatedpickerOptionsAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.model.Approve[0];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") < beginDateVal;
            }
          }
        },

        //结束时间
        finshDatepickerOptionsBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.model.finshDate[1];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") > beginDateVal;
            }
          }
        },
        finshDatepickerOptionsAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.model.finshDate[0];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") < beginDateVal;
            }
          }
        },

        model: {
          selectForm: {
            'searchWord':'',//关键字搜索
            'approveStatus':'',  //流程状态
            'startDate': '', //开始时间
            'finishDate': '', //结束时间
            'amountOfMoneyB': '', //金额低
            'amountOfMoneyE': '', //金额高
            'processFullName': '',//流程类别
            companyCode:'',//公司 （ID）
            departmentID: "", //部门（ID）
            projectId:'',  //项目（ID）
            'originator':'' ,//查询发起人后台
            'orderBy':' startDate desc ', //排序
          },
          startDate:[],//开始时间
          finshDate:[],//结束时间
          submitDate:[],//抄送时间
          Approve:[],//审阅时间
        },
        startDate:true,//控制ie 刷新 开始时间
        endDate:true, //控制ie 刷新 结束时间
        processFormName: '',//流程类别 显示名
        //传给子组件的值
        childDataUrl: {
          url: '',
          selectedPerson:[],
        },
        showdialog: false,
        //动态变更弹出框组件
        comp: {},   // 传给子组件 数据
        loading:false,//loading
        orgStates: [], //发起人  搜索数据
        showList: false, //列表显示状态
        // 行数据-传给子组件
        rowData: {},
        oneCheck:false,//发起人 单选
        //component 弹窗 子组件 所有参数
        componentParam:{
          componentType:'',//弹窗类型
          //子组件api接口路径(一接口名为属性key)
          childDataUrl:{
            type: Object,
            required: true
          },
          //需要 回填到组件上的 数据集合
          backfillData:{
            type: Array,
            required: true
          },
          //子组件是否单选(true单选，false不是单选)
          oneCheck:{
            type: Boolean,
            required: true
          },
          //控制弹窗头部标题显示隐藏(默认显示)
          closeShow:{
            type: Boolean,
            required: true
          },
        },
        pageIndex: 1,
        total: 0,
        pageSize: 20,
        pageSizesList: [20, 50, 100],
        multipleSelection: [], //表格选择数据
        tableData: [], //表格数据
        //流程的tag
        serachSlectData:{
          tagsOwnProcess:[],//tags带来的数据
          tagsOwnCategory:[],//流程类别
          CurrentUser:[],
          tagsOrgSelect:[],
          ToWriteData:[],//提写人数据
          tagsOwnsUser:[],//选人员
          SubmitterData:[],//抄送人数据
        },
        //初始流程状态
        state:[
          {
            "value": "",
            "label": "所有"
          }, {
            "value": 0,
            "label": "错误"
          },
          {
            "value": 1,
            "label": "草稿"
          },
          {
            "value": 2,
            "label":"运行中"
          },
          {
            "value": 3,
            "label":"结束"
          },
          {
            "value": 31,
            "label": "取消"
          },
          {
            "value": 32,
            "label":"作废"
          },
          {
            "value": 33,
            "label":"终审通过"
          },
          {
            "value": 34,
            "label":"拒绝"
          }
        ],
        readState:[
          {
          "value": "",
          "label": "所有"
        },
          {
          "value": "false",
          "label": "待阅"
        },
          {
            "value": "true",
            "label": "已阅"
          },
        ],
        queryName:'', //发起人 搜索输入的值
        isSeach:false, //发起人 搜索 控制开关
        imgLoading: false, //loading
        dataTitle:"",
        originatorPageIndex: 0,
        originatorPageSize: 10,
        originatorTotal: 0,
        CriteriaModel:{
          ProcessNum  :'',//单号
          ProcessCode  :[], //流程名称
          ProcessCategory:[],//流程类别
          FormSubject :'', //主题
          Originator :'', //申请人
          SubmitStartDate :'', //开始时间
          SubmitEndDate :'',  //结束时间
          SubmitterAccount:'',//填单人
          CcStartDate :'',//抄送开始时间
          CcEndDate :'',//抄送结束时间
          ReaderStatus:'',//是否查阅
          ProcessStatus :'', //流程状态
          FinishStartDate:'',
          FinishEndDate:'',
          SortField:'',
          SortDirection:'',
          total:0,//总数量
          pageSizesList: [10,20],
          PageIndex :1, //当前页码
          PageSize :10 //每页数量
        },//获取我的代办初始参数定义
        Visible:false,
        ProcessType:'',//流程类型，用于父组件给子组件
        typeTab:'User',
        portalmMtilselectCheck:'false',//单选多选配置
        echoData:[]
      };
    },
    methods: {
      /**
       * 埋点事件
       */
      burialPointEvent(event){
        util.burialPoint(event);
      },
      // 加载更多
      loadMore(value) {
        this.originatorPageIndex ++;
        this.getUsers(value);
      },
      // 点击input 不显示控件 点击icon图标显示控件
      hideDate(val) {
        util.burialPoint('抄送我的-输入条件-发起时段');
        setTimeout( () => {
          let dateNodeStart = undefined;
          let dateNodeEnd = undefined;
          let startBool = null;
          let endBool = null;
          if (document.getElementsByClassName('el-picker-panel')[0]) {
            startBool = true;
          }
          if (document.getElementsByClassName('el-picker-panel')[1]) {
            endBool = true;
          }

          dateNodeStart = startBool ? document.getElementsByClassName('el-picker-panel')[0] : '';
          dateNodeEnd = endBool ? document.getElementsByClassName('el-picker-panel')[1] : '';

          if (val.$el.firstElementChild == document.activeElement) {
            startBool ? dateNodeStart.style.display = 'none' : '';
            endBool ? dateNodeEnd.style.display = 'none' : ''
          } else {
            startBool ? dateNodeStart.style.display = 'block' : '';
            endBool ? dateNodeEnd.style.display = 'block' : ''
          }
        }, 10)
      },
      //公司、组织、项目 控件 选择后触发
      changeSetvaule(val) {
        let data = val.data;
        this.model.selectForm.companyCode = (data.organize)?data.organize.orgID:''//公司 （ID）
        this.model.selectForm.departmentID = (data.department)?data.department.id:'' //部门（ID）
        this.model.selectForm.projectId = (data.projects)?data.projects.pc_ID:''  //项目（ID）
      },

      //表格排序
      sortChange(column){
        util.burialPoint('我的已办-列表-' + column.column.label + '排序');
        this.CriteriaModel.SortDirection  = `${column.order.replace('ending','')}`;
        this.CriteriaModel.SortField   = column.prop;
        this.getList();
      },
      //设置表格列头
      renderIcon(create, {column}) {
        return create("i", {"class": "iconfont icon-process"});
      },
      validationInput(userName) {
        this.orgStates = [];
        if (userName == "" || userName.length == 0) {
          this.loading = false;
          return null;
        }
        if (userName == this.$refs.userId.query) {
          this.getUsers(userName);
        } else {
          this.orgStates = [];
        }
      },
      //下拉框显示隐藏 清空下拉数据
      visibleChange(){
        this.orgStates = [];
      },
      //刷新查询条件
      refresh() {
        this.CriteriaModel.ProcessNum='';
        this.serachSlectData.tagsOwnProcess = [];
        this.serachSlectData.tagsOwnCategory = [];
        this.CriteriaModel.ProcessCategory = [];
        this.CriteriaModel.Originator = '';
        this.CriteriaModel.DepartmentId = '';
        this.CriteriaModel.FormSubject = '';
        this.serachSlectData.tagsOrgSelect =[];
        this.serachSlectData.SubmitterData =[];
        this.serachSlectData.tagsOwnsUser = [];
        this.serachSlectData.ToWriteData = [];
        this.serachSlectData.CurrentUser = [];
        this.model.startDate = []; //发起时段
        this.model.handtDate = []; //发起时段
        this.model.finshDate = []; //发起时段
        this.model.submitDate = []; //发起时段
        this.model.Approve = []; //发起时段
        this.CriteriaModel.ProcessStatus = this.state[0].value;
        this.CriteriaModel.ReaderStatus = this.readState[0].value
      },
      //金额输入中验证
      inputValidate(position, value) {
        let that = this;
        let regPos = /^([0-9]*)([.]?|(\.\d{1,2})?)$/; //验证金额正则
        if (position == 'front') {
          if (!regPos.test(value)) {
            setTimeout(function () {
              that.model.selectForm.amountOfMoneyB = '';
            }, 100);
          }
        } else if (position == 'back') {
          if (!regPos.test(value)) {
            setTimeout(function () {
              that.model.selectForm.amountOfMoneyE = '';
            }, 100);
          }
        }
      },
      //金额输完验证
      checkValidate(position, value) {
        value = parseFloat(value);
        let that = this;
        if (position == 'front') {
          if (this.model.selectForm.amountOfMoneyE != '' && value > parseFloat(this.model.selectForm.amountOfMoneyE)) {
            that.model.selectForm.amountOfMoneyB = '';
          }
        } else if (position == 'back') {
          if (this.model.selectForm.amountOfMoneyB != '' && value < parseFloat(this.model.selectForm.amountOfMoneyB)) {
            that.model.selectForm.amountOfMoneyE = '';
          }
        }
      },
      //搜索
      search() {
        util.burialPoint('抄送我的-输入条件-搜索按钮');
        this.pageIndex = 1;
        this.getList();
      },
      //获取列表
      getList() {
        var that = this;
        //开始时间
        that.model.startDate == null ? (that.model.startDate = []) : "";
        //结束时间
        that.model.finshDate == null ? (that.model.finshDate = []) : "";

        that.CriteriaModel.SubmitStartDate   = that.model.startDate[0]? `${that.model.startDate[0]} 00:00:00`: "";
        that.CriteriaModel.SubmitEndDate  = that.model.startDate[1]? `${that.model.startDate[1]} 23:59:59`: "";

        that.CriteriaModel.FinishStartDate  = that.model.finshDate[0]? `${that.model.finshDate[0]} 00:00:00`: "";
        that.CriteriaModel.FinishEndDate  = that.model.finshDate[1]? `${that.model.finshDate[1]} 23:59:59`: "";

        that.CriteriaModel.CcStartDate   = that.model.submitDate[0]? `${that.model.submitDate[0]} 00:00:00`: "";
        that.CriteriaModel.CcEndDate   = that.model.submitDate[1]? `${that.model.submitDate[1]} 23:59:59`: "";

        that.CriteriaModel.ApproveStartDate   = that.model.Approve [0]? `${that.model.Approve[0]} 00:00:00`: "";
        that.CriteriaModel.ApproveEndDate   = that.model.Approve[1]? `${that.model.Approve[1]} 23:59:59`: "";

        that.CriteriaModel.ProcessNum = that.CriteriaModel.ProcessNum.replace(/\s+/g,"");
        that.CriteriaModel.FormSubject = that.CriteriaModel.FormSubject.replace(/\s+/g,"");
        //申请人
        if(that.serachSlectData.tagsOwnsUser.length>0){
          that.CriteriaModel.Originator = that.serachSlectData.tagsOwnsUser[0].UserAccount;
        }else {
          that.CriteriaModel.Originator = ''
        }
        //处理人
        if( that.serachSlectData.CurrentUser.length>0){
          that.CriteriaModel.CurrentUser = that.serachSlectData.CurrentUser[0].UserAccount;
        }else {
          that.CriteriaModel.CurrentUser = ''
        }
        //填单人 SubmitterAccount SubmitterAccount
        if( that.serachSlectData.SubmitterData.length>0){
          that.CriteriaModel.SubmitterAccount = that.serachSlectData.SubmitterData[0].UserAccount;
        }else{
          that.CriteriaModel.SubmitterAccount = ''
        }
        //部门
        if( that.serachSlectData.tagsOrgSelect.length>0){
          that.CriteriaModel.DepartmentId = that.serachSlectData.tagsOrgSelect[0].UserAccount;
        }else{
          that.CriteriaModel.DepartmentId = ''
        }
//        //流程
        if( that.serachSlectData.tagsOwnProcess){
          that.CriteriaModel.ProcessCode = [];
          for(let item of this.serachSlectData.tagsOwnProcess){
            that.CriteriaModel.ProcessCode.push(item.processCode);
          }
        }
        //流程类别
        if(that.serachSlectData.tagsOwnCategory){
          that.CriteriaModel.ProcessCategory = [];
          for(let v of that.serachSlectData.tagsOwnCategory){
            that.CriteriaModel.ProcessCategory.push(v.id)
          }
        }
//        if(that.CriteriaModel.ProcessStatus=== ''){
//          that.CriteriaModel.ProcessStatus = that.state[0].value;
//        }
//        if(that.CriteriaModel.ReaderStatus === ''){
//          that.CriteriaModel.ReaderStatus = that.readState[0].value;
//        }
        that.imgLoading = true;
        that.$axios({
          url:'/Portal/WorkFlow/GetMyCCList',
          data:that.CriteriaModel,
          method:'post'
        }).then(result => {
          if(result.data.code === 200) {
            // 已办数据集合
            that.tableData = result.data.data.item;
            // 数据总数
            that.CriteriaModel.total = result.data.data.total;
            for (let item of that.tableData){
              item.FinishDate =  item.FinishDate ? moment(item.FinishDate).format("YYYY-MM-DD HH:mm") : '';
              item.ReceiverEndDate = item.ReceiverEndDate ? moment(item.ReceiverEndDate).format("YYYY-MM-DD HH:mm") : '';
              if(item.ApprovalUsers){
                item.ApprovalUsers = item.ApprovalUsers.split(",");
              }
            }
          }else{
            that.$message.error("请求失败！");
          }
          that.imgLoading = false;
        }).catch(err => {
          // loading.endLoading();
          that.imgLoading = false;
          that.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
        });
      },
      /**
       * 页面size变更方法
       */
      handleSizeChange(val) {
        util.burialPoint('抄送我的-列表-每页显示条数按钮');
        var pageSize = `${val}`;
        this.CriteriaModel.Page = 1;
        this.CriteriaModel.PageSize = parseInt(pageSize);
        localStorage.ccToMePageSize = val;
        this.getList();
      },
      /**
       * 翻页方法
       */
      handleCurrentChange(val) {
        util.burialPoint('抄送我的-列表-页码按钮');
        this.CriteriaModel.PageIndex = val;
        this.getList();
      },
      //设置表头标题class
      tableHeaderClassName({row, rowIndex}) {
        return "table-head-th";
      },
      //全选
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      choosenDialogType(val,type){
        var that = this;
        that.typeTab = val;
        //打开流程弹窗
        that.ProcessType = type;
        that.$refs.dialogChoosen.openChoosen(that.typeTab,type);
        that.$refs.dialogChoosen.getCollect();
      },
      //打开弹窗
      OpenDialog(val){
        var that = this;
        that.Visible = val;
      },
      //关闭弹窗
      CloseDialog(val){
        var that = this;
        that.Visible = val;
      },
      //接收子组件确定按钮带过的数据
      sendTag(val,type){
        let _this = this;
        if(val.length > 0) {
          if (type) {
            for (let item of val) {
              if (type === 'CurrentUser') {
                _this.serachSlectData.CurrentUser = [];
                if (_this.serachSlectData.CurrentUser.length > 0) {
                  for (let i of _this.serachSlectData.CurrentUser) {
                    if (i.SysId !== item.SysId) {
                      _this.serachSlectData.CurrentUser.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.CurrentUser.push(item);
                }
              }
              else if (type === 'User') {
                _this.serachSlectData.tagsOwnsUser = [];
                if (_this.serachSlectData.tagsOwnsUser.length > 0) {
                  for (let i of _this.serachSlectData.tagsOwnsUser) {
                    if (i.SysId !== item.SysId) {
                      _this.serachSlectData.tagsOwnsUser.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.tagsOwnsUser.push(item);
                }
              }
              else if (type === 'Process') {
                if (_this.serachSlectData.tagsOwnProcess.length > 0) {
                  for (let i of _this.serachSlectData.tagsOwnProcess) {
                    if (i.id !== item.id) {
                      _this.serachSlectData.tagsOwnProcess.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.tagsOwnProcess.push(item);
                }
              }
              else if (type === 'Category') {
                if (_this.serachSlectData.tagsOwnCategory.length > 0) {
                  for (let i of _this.serachSlectData.tagsOwnCategory) {
                    if (i.id !== item.id) {
                      _this.serachSlectData.tagsOwnCategory.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.tagsOwnCategory.push(item);
                }
              }
              else if (type === 'ToWrite') {
                if (_this.serachSlectData.ToWriteData.length > 0) {
                  for (let i of _this.serachSlectData.ToWriteData) {
                    if (i.id !== item.id) {
                      _this.serachSlectData.ToWriteData.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.ToWriteData.push(item);
                }
              }
              else if(type==='Submitter'){
                if (_this.serachSlectData.SubmitterData.length > 0) {
                  for (let i of _this.serachSlectData.SubmitterData) {
                    if (i.id !== item.id) {
                      _this.serachSlectData.SubmitterData.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.SubmitterData.push(item);
                }
              }
              else if (type==='Org') {
                _this.serachSlectData.tagsOrgSelect = [];
                if (_this.serachSlectData.tagsOrgSelect.length > 0) {
                  for (let i of _this.serachSlectData.tagsOrgSelect) {
                    if (i.SysId !== item.SysId) {
                      _this.serachSlectData.tagsOrgSelect.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.tagsOrgSelect.push(item);
                }
              }
            }
          }
        }else {
          switch (type){
            case 'Process':{
              _this.serachSlectData.tagsOwnProcess = [];
              break
            }
            case 'Category':{
              _this.serachSlectData.tagsOwnCategory = [];
              break;
            }
            case 'User':{
              _this.serachSlectData.tagsOwnsUser = [];
              break;
            }
            case 'Org':{
              _this.serachSlectData.tagsOrgSelect = [];
              break
            }
            case 'CurrentUser':{
              _this.serachSlectData.CurrentUser = [];
              break
            }
            case 'Submitter':{
              _this.serachSlectData.SubmitterData = [];
              break
            }
          }
        }
      },
      //删除页面回显中的tag
      closeProcessTag(item){
        let _this = this;
        if(item.portalType){
          switch (item.portalType){
            case 'User':{
              let index = _this.serachSlectData.tagsOwnsUser.indexOf(item);
              _this.serachSlectData.tagsOwnsUser.splice(index,1);
              _this.CriteriaModel.Originator = '';
              _this.$refs.dialogChoosen.portalehco(_this.serachSlectData.tagsOwnsUser,null);
              break;
            }
            case 'Submitter':{
              let index = this.serachSlectData.SubmitterData.indexOf(item);
              _this.serachSlectData.SubmitterData.splice(index,1);
              _this.CriteriaModel.SubmitterAccount = '';
              _this.$refs.dialogChoosen.portalehco(_this.serachSlectData.SubmitterData,null);
              break;
            }
            case 'CurrentUser':{
              let index = this.serachSlectData.CurrentUser.indexOf(item);
              _this.CriteriaModel.CurrentUser = '';
              _this.serachSlectData.CurrentUser.splice(index,1);
              _this.$refs.dialogChoosen.portalehco(_this.serachSlectData.CurrentUser,null);
              break
            }
            case 'Process':{
              let index =  _this.serachSlectData.tagsOwnProcess.indexOf(item);
              _this.serachSlectData.tagsOwnProcess.splice(index,1);
              _this.$refs.dialogChoosen.portalehco(_this.serachSlectData.tagsOwnProcess);
              break;
            }
            case 'Category':{
              let index =  _this.serachSlectData.tagsOwnCategory.indexOf(item);
              _this.serachSlectData.tagsOwnCategory.splice(index,1);
              _this.$refs.dialogChoosen.portalehco(_this.serachSlectData.tagsOwnCategory);
              break;
            }
          }
        }else {
          switch (item.type){
            case 'Process':{
              let index =  _this.serachSlectData.tagsOwnProcess.indexOf(item);
              _this.serachSlectData.tagsOwnProcess.splice(index,1);
              _this.$refs.dialogChoosen.portalehco(_this.serachSlectData.tagsOwnProcess);
              break;
            }
            case 'Category':{
              let index =  _this.serachSlectData.tagsOwnCategory.indexOf(item);
              _this.serachSlectData.tagsOwnCategory.splice(index,1);
              _this.$refs.dialogChoosen.portalehco(_this.serachSlectData.tagsOwnCategory);
              break;
            }
            case 'User':{
              let index = _this.serachSlectData.tagsOwnsUser.indexOf(item);
              _this.serachSlectData.tagsOwnsUser.splice(index,1);
              _this.CriteriaModel.Originator = '';
              _this.$refs.dialogChoosen.portalehco(_this.serachSlectData.tagsOwnsUser,null);
              break;
            }
            case 'Org':{
              let index = this.serachSlectData.tagsOrgSelect.indexOf(item);
              _this.serachSlectData.tagsOrgSelect.splice(index,1);
              _this.CriteriaModel.DepartmentId = '';
              _this.$refs.dialogChoosen.portalehco(_this.serachSlectData.tagsOrgSelect,null);
              break;
            }
          }
        }
      },
      /**
       * 点击主题跳转到旧界面
       */
      jumpToView(row) {
        window.open(row.FlowViewUrl,'_blank');
        var mychar = document.getElementById('dailog_mask');
        mychar.style.display = 'none';
      },
      //流程单号跳转
      folioBlank(row){
        window.open(row.Url,'_blank');
      },
      //初始设置是否勾选
      checkBoxForm(row,index){
        if(row.IsBatchApproval){
          return 1;
        }else{
          return 0;
        }
      },
    },
    watch: {
      'model.userName' :{
        handler: function (n, o) {
          let ele = this.$refs.userId
          util.inputHeight(n,ele)
          this.dataTitle ='';
          // 监听数组list 的第一项的name值变化；n 为新数据，o 为旧数据
          for(var i=0;i<n.length;i++){
            if(typeof n[i] == 'object'){
              this.dataTitle +=(n[i].value+',');
            }else if(typeof n[i] == 'string'){
              this.dataTitle +=(n[i]+',');
            }
          }
          this.dataTitle = this.dataTitle.substr(0,this.dataTitle.length-1);
        },
        deep: true
      },
       Visible:{
        handler(n, o) {
          if(n){
            switch (this.typeTab){
              case 'User':{
                this.echoData = [...this.serachSlectData.tagsOwnsUser];
                break
              }
              case 'Org':{
                this.echoData = [...this.serachSlectData.tagsOrgSelect];
                break
              }
            }
            switch (this.ProcessType){
              case 'Process':{
                this.echoData = [...this.serachSlectData.tagsOwnProcess];
                break
              }
              case 'Category':{
                this.echoData = [...this.serachSlectData.tagsOwnCategory];
                break
              }
              case 'CurrentUser':{
                this.echoData = [...this.serachSlectData.CurrentUser];
                break
              }
              case 'ToWrite':{
                this.echoData = [...this.serachSlectData.ToWriteData];
                break
              }
              case 'Submitter':{
                  this.echoData = [...this.serachSlectData.SubmitterData];
                  break
                }
            }
          }
        }
      },
      "model.date":{
        handler: function(n, o) {
          this.startDate = false
          this.$nextTick(() => {
            this.startDate = true
          })
        this.endDate = false
          this.$nextTick(() => {
            this.endDate = true
          })
        },
        deep: true
      }
    },
    computed: {
      bodyHeight() {
        return document.documentElement.clientHeight
      },
      ...mapGetters(['isIE','isSafari', 'bodyHeight'])
    }
  };
</script>

